@media screen and (max-width: 1680px) {
    .container {
        padding: 0;
    }

    header {

        .logo-cnt {
            .logi-icon {
                width: 48px;
                height: 34px;
            }

            .logo-title {
                padding: 2px 0 0 10px;
                line-height: 15px;
            }
        }
        
        .nav-menu {
            li {
                font-size: 15px;
                margin: 0 0 0 43px;
                padding: 8px 0;

                .nav-link {
                    &:before {
                        width: 12px;
                        top: -8px;
                    }
                }
            }
        }
    }

    .inside-page-content {
        .main-title-container {
            padding: 197px 0 0;

            h1 {
                font-size: 100px;
                letter-spacing: -1px;
            }
        }
        
        .service-content-wrapper {
            margin: 70px 0 100px;

            .service-desc {
                p {
                    font-size: 18px;
                    line-height: 25px;
                }
            }

            .top-service-cnt {
                margin: 0; 
                padding: 0 100px;

                ul {
                    li {
                        margin: 75px 3.122222% 0;

                        .icon-cnt {
                            height: 60px;
                            margin: 0 0 35px;
                        }

                        .list-desc {
                            font-size: 16px;
                            line-height: 22px;

                            span {
                                font-size: 18px;
                            }
                        }
                    }
                }
            }

            .our-offer-cnt {
                margin: 88px 0 0;

                .offer-title {
                    h2 {
                        font-size: 50px;
                    }
                }

                .our-offer-desc-cnt {
                    width: 1050px;

                    ul {
                        margin: 0;

                        li {
                            margin: 63px 0 0;

                            .icon-cnt {
                                margin: 0 0 35px;

                                .inner-ico-cnt {
                                    width: 87px;
                                    height: 87px;
                                }
                            }

                            .desc-cnt {
                                .title {
                                    margin: 0 0 4px;
                                    font-size: 18px;
                                    line-height: 23px;
                                }

                                .description {
                                    font-size: 16px;
                                    line-height: 22px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    footer {
        height: 100px;

        .copyright {
            max-width: 800px;
            padding: 4px 0;
            font-size: 13px;
        }
    }
}

@media screen and (max-width: 1440px) {
    .container {
        padding: 0;
    }

    header {
        height: 85px;
        padding: 0 85px;

        .logo-cnt {
            .logi-icon {
                width: 40px;
                height: 29px;
            }

            .logo-title {
                font-size: 16px;
                line-height: 14px;
                padding: 0 0 0 10px;
            }
        }

        .nav-menu {
            li {
                font-size: 14px;
                margin: 0 0 0 36px;
                padding: 7px 0;

                .nav-link {
                    &:before {
                        width: 11px;
                        top: -7px;
                    }
                }
            }
        }
    }

    footer {
        height: 85px;

        .copyright {
            padding: 3px 0;
            max-width: 650px;
            font-size: 12px;
        }
    }

    .inside-page-content {
        .main-title-container {
            padding: 177px 0 0;

            h1 {
                font-size: 80px;
            }
        }

        .service-content-wrapper {
            margin: 60px 0 85px;

            .service-desc {
                p {
                    margin: 10px 0;
                    font-size: 16px;
                    line-height: 22px;
                }
            }

            .top-service-cnt {
                margin: 25px 0 0;
                padding: 0 85px;

                ul {
                    justify-content: center;

                    li {
                        width: 190px;
                        margin: 50px 40px 0;

                        &:nth-child(5n+1) {
                            margin-left: 40px;
                        }
                        &:nth-child(5n+5) {
                            margin-right: 40px;
                        }

                        .icon-cnt {
                            height: 50px;
                            margin: 0 0 28px;
                        }

                        .list-desc {
                            font-size: 14px;
                            line-height: 20px;

                            span {
                                font-size: 16px;
                            }
                        }
                    }
                }
            }

            .our-offer-cnt {
                margin: 85px 0 0;

                .our-offer-desc-cnt {
                    width: 950px;

                    ul {
                        margin: 15px 0 0;

                        li {
                            margin: 55px 0 0;
                            padding: 0 3.5% 0 0;

                            .icon-cnt {
                                margin: 0 0 30px;

                                .inner-ico-cnt {
                                    width: 75px;
                                    height: 75px;
                                }
                            }

                            .desc-cnt {
                                .title {
                                    font-size: 17px;
                                    line-height: 22px;
                                }

                                .description {
                                        font-size: 15px;
                                        line-height: 21px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1152px) {
    .container {
        padding: 0 38px;
    }

    header {
        justify-content: flex-start;
        height: 88px;
        padding: 0 38px;
        background: -webkit-gradient(linear,left top,left bottom,from(white),color-stop(10%,#fff),to(rgba(255,255,255,0)));
        background: linear-gradient(to bottom,#fff 0,#fff 10%,rgba(255,255,255,0) 100%);

        .logo-cnt {
            .logo-title {
                font-size: 19px;
                line-height: 21px;
                padding: 0 0 0 10px;
            }
        }
        
        .nav-menu {
            display: none;
        }

        .open-menu {
            display: inline-block;
        }
    }

    footer {
        height: 96px;

        .copyright {
            padding: 0;
            max-width: calc(~"100% - 76px");
            font-size: 14px;
        }
    }

    .inside-page-content {
        .main-title-container {
            padding: 152px 0 0;

            h1 {
                font-size: 70px;
            }
        }

        .service-content-wrapper {
            margin: 33px 0 69px;

            .service-desc {
                p {
                    margin: 13px 0;
                    font-size: 17px;
                    line-height: 23px;
                }
            }

            .top-service-cnt {
                margin: 78px 0 0;
                padding: 0;

                ul {
                    justify-content: center;

                    li {
                        width: 50%;
                        margin: 0 0 59px;
                        padding: 0 10% 0 0;

                        &:nth-child(2n+2) {
                            padding: 0 0 0 10%;
                        }
                        &:nth-child(5n+1) {
                            margin-left: 0px;
                        }
                        &:nth-child(5n+5) {
                            margin-right: 0px;
                        }

                        .icon-cnt {
                            height: 69px;
                            margin: 0 0 31px;
                        }

                        .list-desc {
                            font-size: 17px;
                            line-height: 23px;

                            span {
                                font-size: 21px;
                            }
                        }
                    }
                }
            }

            .our-offer-cnt {
                margin: 123px 0 0;

                .offer-title {
                    h2 {
                        font-size: 60px;
                    }
                }

                .our-offer-desc-cnt {
                    // width: 100%;
                    width: calc(~"100% - 96px");

                    ul {
                        margin: 15px 0 0;

                        li {
                            margin: 40px auto 25px;
                            padding: 0;
                            width: 100%;
                            max-width: 600px;

                            .icon-cnt {
                                margin: 0 0 37px;

                                .inner-ico-cnt {
                                    width: 107px;
                                    height: 107px;
                                    display: inline-block;
                                }
                            }

                            .desc-cnt {
                                text-align: center;

                                .title {
                                    font-size: 20px;
                                    line-height: 26px;
                                }

                                .description {
                                    font-size: 18px;
                                    line-height: 24px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 640px) {
    footer {
        .copyright {
            padding: 2px 0 0;
            font-size: 13px;
        }
    }

    header {
        &.opened {
            .mp-menu-b {
                top: 68px;

                .inner-mp-menu-b {
                    .list-mp-menu {
                        li {
                            &:nth-child(7) {
                                margin-bottom: 100px;
                            }
                        }
                    }
                }
            }
        }
    }
    
    .inside-page-content {
        &.animate-element {
            transform: translateY(5%) scale(1) ;
        }

        .service-content-wrapper {
            .top-service-cnt {
                ul {
                    li {
                        &:nth-child(2n+2) {
                            padding: 0 0 0 7%;
                        }
                    }
                }
            }

            .our-offer-cnt {
                margin: 123px 0 0;

                &.animate-element {
                    transform: translateY(2%) scale(1) ;
                }

                .offer-title {
                    h2 {
                        font-size: 60px;
                    }
                }

                .our-offer-desc-cnt {
                    width: 100%;
                }
            }
        }
    }
}

@media screen and (max-width: 740px) and (max-height: 425px)
and (orientation: landscape) {

    header {
        .mp-menu-b {
            .inner-mp-menu-b {
                .list-mo-menu {
                    li {
                        &:nth-child(7) {
                            margin-bottom: 100px;
                        }
                    }
                }
            }
        }
    }

    .inside-page-content {
        .service-content-wrapper {
            .top-service-cnt {
                margin: 70px 0 0;

                ul {
                    li {
                        margin: 0 0 56px; 
                        padding: 0 5% 0 0;

                        &:nth-child(2n+2) {
                            padding: 0 0 0 5%;
                        }
                        &:last-child {
                            padding: 0 2.5%;
                        }
                        

                        .icon-cnt {
                            height: 62px; 
                            margin: 0 0 22px;
                        }

                        .list-desc {
                            font-size: 16px;
                            padding: 0 10%;
                        }
                    }
                }
            }

            .our-offer-cnt {
                margin: 83px 0 0;

                .offer-title {
                    h2 {
                        display: block;
                        font-size: 45px;
                        text-align: center;
                    }
                }

                .our-offer-desc-cnt {
                    width: 100%;

                    ul {
                        margin: 22px 0 0 0; 
                        justify-content: center;

                        li {
                            width: 50%;
                            margin: 40px 0 15px;
                            padding: 0 5% 0 0;

                            &:last-child {
                                padding: 0 2.5%;
                            }
                            &:nth-child(2n+2) {
                                padding: 0 0 0 5%;
                            }

                            .icon-cnt {
                                margin: 0 0 27px;

                                .inner-ico-cnt {
                                    width: 87px;
                                    height: 87px;
                                }
                            }

                            .desc-cnt {
                                text-align: center;

                                .title {
                                    font-size: 18px;
                                    line-height: 24px;
                                }

                                .description {
                                    font-size: 16px;
                                    line-height: 22px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}


