@import 'fonts.css';

* {
  margin: 0;
  padding: 0;
  .box-sizing;
}

a {
    display: inline-block;
    text-decoration: none;
    color: #ffffff;
    cursor: pointer;
}

ul {
    list-style: none;
}

.box-sizing (@type: border-box) {
  box-sizing: @type;
  -webkit-box-sizing: @type;
  -moz-box-sizing: @type;
  -ms-box-sizing: @type;
}

body {
    width: 100%;
    font-size: 16px;
    color: #000000;
    font-weight: 400;
    text-align: center;
    font-family: "ProximaNovaSemiBold";

    &.locked {
        overflow: hidden!important;
    }
}

.container {
    width: 100%;
    max-width: 1700px;
    margin: 0 auto;
    position: relative;
}

i {
    font-style: normal;
}

.icon {
    display: inline-block;
    background-image: url(../images/icon/icons-set.png);
    background-repeat: no-repeat;
    background-position: center;
}

#app {
    overflow: hidden;
}

.mob-br {
    display: none;
}
.hd-br {
    display: inline-block;
}

header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 110px;
    position: fixed;
    top: 0;
    margin: 0 auto;
    padding: 0 110px;
    background: -webkit-gradient(linear,left top,left bottom,from(white),color-stop(55%,#fff),to(rgba(255,255,255,0)));
    background: linear-gradient(to bottom,#fff 0,#fff 55%,rgba(255,255,255,0) 100%);
    z-index: 10;

    &.animate-element {
        opacity: 0;
        transform: translateY(-73%) scale(1);
        transition: 1.5s cubic-bezier(.4,0,0,1);
        
        &.active {
            opacity: 1;
            transform: none;
        }
    }

    .logo-cnt {
        display: flex;
        flex-direction: row;
        z-index: 2;

        .logo-icon {
            width: 53px;
            height: 38px;
            background: url(../images/svg/logo.svg) center no-repeat;
            background-size: 100% 100%;
        }

        .logo-title {
            padding: 0 0 0 12px;
            font-family: "ProximaNovaExtraBold";
            font-size: 20px;
            line-height: 22px;
            text-align: left;

            i {
                font-size: 30px;
            }
        }
    }

    .nav-menu {
        display: flex;
        -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
        width: auto;
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            width: auto;
            display: inline-block;
            margin: 0 0 0 53px;
            padding: 10px 0;
            overflow: hidden;
            position: relative;
            opacity: 1;
            -webkit-transform: none;
            transform: none;
            
            &:before {
                content: '';
                z-index: 2;
                position: absolute;
                width: 100%;
                height: 100%;
                -webkit-transform-origin: right center;
                transform-origin: right center;
                -webkit-transition: -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
                transition: -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
                transition: transform 250ms cubic-bezier(.4,0,.2,1);
                transition: transform 250ms cubic-bezier(.4,0,.2,1),-webkit-transform 250ms cubic-bezier(.4,0,.2,1);
            }

            // &:hover {
            //     &:before {
            //         -webkit-transform: scaleX(0);
            //         transform: scaleX(0);
            //     }
            // }

            .nav-link {
                display: inline-block;
                font-family: "ProximaNovaExtraBold";
                color: #000000;
                position: relative;
                z-index: 3;
                -webkit-transition: color .3s cubic-bezier(.455,.03,.515,.955);
                transition: color .3s cubic-bezier(.455,.03,.515,.955);

                &:before {
                    content: '';
                    position: absolute;
                    width: 15px;
                    height: 2px;
                    background: #000;
                    left: 0;
                    top: -10px;
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                    -webkit-transform-origin: right center;
                    transform-origin: right center;
                    -webkit-transition: -webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
                    transition: -webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
                    transition: transform .3s cubic-bezier(.455,.03,.515,.955);
                    transition: transform .3s cubic-bezier(.455,.03,.515,.955),-webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
                }

                &:hover {
                    &:before {
                        -webkit-transform: scaleX(1);
                        transform: scaleX(1);
                        -webkit-transform-origin: left center;
                        transform-origin: left center;
                    }
                }

                i {
                    font-size: 24px;
                }

                &:hover {
                    color: #20aa97;
                }

                &.active {
                    color: #20aa97;

                    &:before {
                        -webkit-transform: scaleX(1);
                        transform: scaleX(1);
                        -webkit-transform-origin: left center;
                        transform-origin: left center;
                    }
                }
            }
        }
    }

    .open-menu, .close-menu {
        display: none;
        padding: 0;
        border: none;
        position: absolute;
        // right: 0;
        right: 6%;
        top: 0;
        bottom: 0;
        margin: auto 0;
        outline: 0;
        cursor: pointer;
    }
    .open-menu {
        width: 24px;
        height: 19px;
        background: 0 0;
        opacity: 0;
        -webkit-transform: translateY(100%);
        transform: translateY(100%);
        visibility: hidden;
        -webkit-animation: openBtnToClose .2s ease-out normal forwards;
        animation: openBtnToClose .2s ease-out normal forwards;
        -webkit-animation-delay: .7s;
        animation-delay: .7s;
        
        &:hover {
            .line-menu {
                background: #20AA97;
            }
        }

        .line-menu {
            display: block;
            height: 3px;
            left: 0;
            position: absolute;
            background: #000000;
            -webkit-transition: all .3s ease-out;
            transition: all .3s ease-out;

            &.line-menu-1, &.line-menu-3 {
                width: 100%;
            }

            &.line-menu-1, &.line-menu-2 {
                top: 0;
            }

            &.line-menu-2, &.line-menu-3 {
                bottom: 0;
            }

            &.line-menu-2 {
                width: 20px;
                margin: auto;
            }
        }
    }
    .close-menu {
        width: 21px;
        height: 21px;
        opacity: 0;
        visibility: hidden;
        background: 0 0;
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
        z-index: 3;

        &:hover {
            &:after {
                opacity: 1;
            }

            &:before {
                opacity: 0;
            }
        }

        &:before,  &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            -webkit-transition: all .3s ease-out;
            transition: all .3s ease-out;
            background-position: center;
            background-repeat: no-repeat;
        }

        &:after {
            opacity: 0;
            background-image: url(../images/svg/close-mp-hv.svg);
            background-size: 100% 100%;
        }

        &:before {
            background-image: url(../images/svg/close-mp.svg);
            background-size: 100% 100%;
        }
    }

    .mp-menu-b {
        width: 100%;
        height: 0;
        position: fixed;
        left: 0;
        top: 88px;
        right: 0;
        margin: 0 auto;
        background: #fff;
        overflow-x: hidden;
        overflow-y: auto;
        z-index: 1;
        -webkit-animation: openMenu .7s cubic-bezier(.4,0,0,1) normal forwards;
        animation: openMenu .7s cubic-bezier(.4,0,0,1) normal forwards;

        .inner-mp-menu-b {
            width: 100%;
            height: 100%;
            position: relative;
            padding: 60px 39px 0;

            .list-mp-menu {
                width: 100%;
                margin: 0;
                padding: 0;
                list-style: none;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;

                li {
                    width: 100%;
                    display: block;
                    font-family: "ProximaNovaExtraBold";
                    font-size: 33px;
                    margin-bottom: 55px;
                    overflow: hidden;
                    position: relative;
                    opacity: 0;
                    -webkit-transform: translateY(-50px);
                    transform: translateY(-50px);
                    -webkit-transition: all .2s ease-out;
                    transition: all .2s ease-out;
                    text-align: left;

                    &.active {
                        a {
                            color: #20aa97;
                        }
                    }

                    a {
                        max-width: 100%;
                        position: relative;
                        text-decoration: none;
                        color: black;
                    }
                }
            }
        }
    }

    &.opened {
        background: #ffffff;

        .mp-menu-b {
            .inner-mp-menu-b {

                .list-mp-menu {
                    li {
                        opacity: 1;
                        transform: none;

                        &:first-child {
                            transition-delay: 100ms;
                        }
                        &:nth-child(2) {
                            transition-delay: 200ms;
                        }
                        &:nth-child(3) {
                            transition-delay: 300ms;
                        }
                        &:nth-child(4) {
                            transition-delay: 400ms;
                        }
                        &:nth-child(5) {
                            transition-delay: 500ms;
                        }
                        &:nth-child(6) {
                            transition-delay: 600ms;
                        }
                        &:nth-child(7) {
                            transition-delay: 800ms;
                        }
                    }
                }
            }
        }

        .close-menu {
            display: inline-block;
            animation: closeBtnToOpen 200ms ease-out normal forwards; 
            animation-delay: 1s;
        }

        .open-menu {
            animation: openBtnToOpen 200ms ease-out normal forwards;
        }
    }

    &.closed {
        .close-menu {
            opacity: 1;
            -webkit-transform: none;
            transform: none;
            visibility: visible;
            -webkit-animation: closeBtnToClose .2s ease-out normal forwards;
            animation: closeBtnToClose .2s ease-out normal forwards;
        }

        .open-menu {
            opacity: 0;
            transform: translateY(100%);
            visibility: hidden;
            animation: openBtnToClose 200ms ease-out normal forwards;
            animation-delay: 700ms;
        }

        .mp-menu-b {
            height: 100%;
            -webkit-animation: closeMenu .7s cubic-bezier(.4,0,0,1) normal forwards;
            animation: closeMenu .7s cubic-bezier(.4,0,0,1) normal forwards;
            -webkit-animation-delay: .6s;
            animation-delay: .6s;

            .inner-mp-menu-b {
                .list-mp-menu {
                    li {
                        opacity: 0; 
                        transform: translateY(-50px);

                        &:first-child {
                            transition-delay: 100ms;
                        }
                        &:nth-child(2) {
                            transition-delay: 200ms;
                        }
                        &:nth-child(3) {
                            transition-delay: 300ms;
                        }
                        &:nth-child(4) {
                            transition-delay: 400ms;
                        }
                        &:nth-child(5) {
                            transition-delay: 500ms;
                        }
                        &:nth-child(6) {
                            transition-delay: 600ms;
                        }
                        &:nth-child(7) {
                            transition-delay: 800ms;
                        }
                    }
                }
            }
        }

        .list-mp-menu {
            li {
                opacity: 0;
                -webkit-transform: translateY(-50px);
                transform: translateY(-50px);

                &:first-child {
                    -webkit-transition-delay: .1s;
                    transition-delay: .1s;
                }
            }
        }
    }
}

footer {
    height: 130px;
    width: 100%;
    position: relative;
    background: #000;
    z-index: 10;
    display: flex;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;

    .copyright {
        display: flex;
        align-items: center;
        width: auto;
        max-width: 1000px;
        overflow: hidden;
        padding: 6px 0;
        color: #fff;
        font-size: 14px;

        a {
            display: inline-block;
            color: #20aa97;
            font-family: "ProximaNovaExtraBold";
            position: relative;

            &:hover {
                &:before {
                    -webkit-transform: scaleX(1);
                    transform: scaleX(1);
                    -webkit-transform-origin: left center;
                    transform-origin: left center;
                }
            }

            &:before {
                content: '';
                position: absolute;
                width: 100%;
                height: 2px;
                left: 0;
                bottom: -4px;
                background: #20aa97;
                -webkit-transform: scaleX(0);
                transform: scaleX(0);
                -webkit-transform-origin: right center;
                transform-origin: right center;
                -webkit-transition: -webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
                transition: -webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
                transition: transform .3s cubic-bezier(.455,.03,.515,.955);
                transition: transform .3s cubic-bezier(.455,.03,.515,.955),-webkit-transform .3s cubic-bezier(.455,.03,.515,.955);
            }
        }
    }
}

h1, h2 {
    margin: 0;
}

@keyframes circleLoader1 {
  0% {opacity: 1; transform: scale(1);}
  50% {transform: scale(.8); opacity: .8;}
  75% {transform: scale(1.2);}
}

@keyframes circleLoader2 {
  0% {transform: scale(1); opacity: 1;}
  25% {transform: scale(1.05); opacity: .55;}
  50% {background: none; opacity: 1; transform: scale(1);}
  75% {transform: scale(.6); background: #20AA97;}
}

@keyframes circleMainScreen {
  0% {transform: none;}
  25% {transform: scale(.9)}
  50% {transform: scale(1.05);}
  75% {transform: scale(.9);}
}

@keyframes blinkCircleLogo {
  90% {transform: none; animation-timing-function: ease-in;}
  93% {transform: translateY(30px) scaleY(0);}
  100% {animation-timing-function: ease-out;}
}

@keyframes squeezeLinesLogo {
  90% {transform: none; animation-timing-function: ease-in;}
  93% {transform: translateY(5px) scaleY(0.8);}
  100% {animation-timing-function: ease-out;}
}

@keyframes closeBtnToOpen {to {opacity: 1; visibility: visible; transform: none;}}
@keyframes openBtnToOpen {to {opacity: 0; visibility: hidden; transform: translateY(100%);}}

@keyframes closeBtnToClose {to {opacity: 0; visibility: hidden; transform: translateY(-100%);}}
@keyframes openBtnToClose {to {opacity: 1; visibility: visible; transform: none;}}

@keyframes openMenu {to {height: 100%;}}
@keyframes closeMenu {to {height: 0;}}

.inside-page-content {
    position: relative;

    &.animate-element {
        opacity: 0;
        transform: translateY(12%) scale(1) ;
        transition: 1.5s cubic-bezier(.4,0,0,1);
        &.active {
            opacity: 1;
            transform: none;
        }
    }

    &:before {
        content: '';
        width: 20.26vw;
        height: 20.26vw;
        right: -5.26vw;
        position: absolute;
        pointer-events: none;
        top: -4vw;
        z-index: 2;
        overflow: hidden;
        border-radius: 100%;
        background: linear-gradient(45deg,rgba(32,170,151,.05) 0,#fff 100%);
        -webkit-animation: circleMainScreen 5s cubic-bezier(.455,.03,.515,.955) infinite alternate;
        animation: circleMainScreen 5s cubic-bezier(.455,.03,.515,.955) infinite alternate;
    }

