            font-family: "ProximaNovaExtraBold";
            font-size: 100px;
            letter-spacing: -1px;

            &.animate-element {
                opacity: 0;
                transform: translateY(50%) scale(.8);
                transition: 2s cubic-bezier(.4,0,0,1);
                &.active {
                    opacity: 1;
                    transform: none;
                }
            }

            i {
                color: #20aa97;
            }
        }
    }

